body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }


.g-card {
    margin: 20px;
}

.g-card-image {
    border-radius: 10px;
    width: 300px;
    height: 400px;
    box-shadow: 0px 0px 3px 1px #ccc;
}

/* gcardimage height was 480 while width was 300  */

.g-card-info {
    margin-top: 10px;
    min-height: 100px;
}

.g-card-title {
    font-size: 24px;
    margin: 0px;
}

.g-card-sub-title {
    font-size: 16px;
    margin: 0px;
}

.success-msg {
    color: #57A773;
    padding: 10px 15px;
}
.success-msg {
    color: #EE6352;
    padding: 10px 15px;
}